.App {
  font-family: sans-serif;
  text-align: center;
}
.btn {
  background-color: #111111; /* Green */
  border: none;
  color: white;
  padding: 10px 15px;
  text-align: center;
  margin: 5px;
  text-decoration: none;
  display: inline-block;
  font-size: 10px;
}
.btn-del {
  background-color: #f7042d; /* Green */
  border: none;
  color: white;
  padding: 5px 15px;
  text-align: center;
  margin-left: 20px;
  margin-top: 5px;
  text-decoration: none;
  display: inline-block;
  font-size: 10px;
}
input {
  width: auto;
  padding: 5px;
}
.list-bkg {
  float: center;
  margin: auto;
  max-width: 500px;
  /* my goal is that the width always fills up independent of browser width */
  width: calc(
    100% - 50%
  ); /* Use calc to remove the fixed width and margin width from the 100% width */
  height: 500px;
  margin-top: 50px;
}
